@import '../../styles/variables.scss';

.Login {
  margin-left: $section-padding;
  padding: 10px;
  display: flex;
  flex-direction: column;
  background: #7dac8ce5;
  border-radius: 5px;

  width: 300px;

  &__title {
    margin-bottom: 10px;
  }

  .Input {
    margin-bottom: 8px;
  }

  &__btn {
    cursor: pointer;
    display: block;
    padding: 5px;
    border-radius: 5px;
    background-color: #58906a;
  }
}