.AddForm {
  background-color: #d9d9d9;
  padding: 5px;
  border-radius: 10px;

  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  flex-direction: column;
  width: 300px;
  height: 350px;

  &__label {
    display: flex;
    justify-content: space-between;
  }
}
.overlay {
    position: fixed;
    background-color: #2e6f43e5;
    width: 100vw;
    height: 100vh;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }