.Pagination {
  text-align: center;

  button {
    background: #75a574;
    border-radius: 5px;
    padding: 10px;
  }

  
  &__btn {
    transition: all 0.5 ease;
    cursor: pointer;
    
    &:hover {
      background: #5ba45a;
    }
  }

  .__not-btn {
    cursor: default;
  }

  button:not(:first-of-type) {
    margin-left: 5px;
  }
}